<template>

    <div class="container">
        <div class="row">
            <div class="col-12">

                <div class="ratio-aspect">
                    <h1>比例計算機</h1>

                    <div class="inner-wrap">
                        <div class="control-wrap">

                            <fieldset>
                                <h4>寬度 (width): <input type="text" v-model="data.width" @input="getRatio"></h4>
                                <h4>高度 (height): <input type="text" v-model="data.height" @input="getRatio"></h4>
                                <span v-show="flag_zero" class="text-danger">請輸入正數</span>
                                <span v-show="flag_compare" class="text-danger">建議輸入「寬>高」的數字</span>
                            </fieldset>

                            <fieldset>
                                <h3>比例 (aspect-ratio):</h3>
                                <h2 v-if="data.ratio" class="text-danger">
                                    {{ data.width_ratio }}:{{ data.height_ratio }}
                                    或
                                    {{ data.width_ratio * 2 }}:{{ data.height_ratio * 2 }}
                                </h2>
                            </fieldset>
                            
                        </div>
                        
                        <!-- 圖形 -->
                        <RatioAspectGraph :ratio="data.ratio" />

                    </div>
                </div>

            </div>
        </div>
    </div>
    
    

</template>

<script>
import { ref, reactive } from 'vue';
import RatioAspectGraph from './RatioAspectGraph.vue'

export default {
    name: 'RatioAspect',
    components: {
        RatioAspectGraph
    },

    setup() {
        // 數據
        const data = reactive({
            width: 0,
            height: 0,

            width_ratio: null,
            height_ratio: null,
            ratio: null,
        })

        let flag_zero = ref(false)
        let flag_compare = ref(false)

        // 方法
        
        // 最大公因數
        //https://kim85326.github.io/2018/03/15/%E6%9C%80%E5%A4%A7%E5%85%AC%E5%9B%A0%E6%95%B8-%E6%9C%80%E5%B0%8F%E5%85%AC%E5%80%8D%E6%95%B8/
        function gcd(m, n) {
            var remainder = 0;
            do {
                remainder = m % n;
                m = n;
                n = remainder;
            } while (remainder !== 0);
            return m;
        }

        // 寬高比例
        let timer;
        function getRatio() {
            // 防抖
            clearTimeout(timer)
            timer = setTimeout(()=>{
                flag_zero.value = false

                // 檢查欄位值是否為0
                if ( data.width <=0 || data.height <=0 ) {
                    flag_zero.value = true
                    return
                }

                // 檢查高度是否大於寬度
                flag_compare.value = +data.width < +data.height ? true : false

                

                // 計算比例
                let gcd_value = gcd(data.width, data.height);
                data.width_ratio = data.width / gcd_value;
                data.height_ratio = data.height / gcd_value;
                data.ratio = `${data.width_ratio}/${data.height_ratio}`
            }, 300)
        }

        

        return {
            data,
            flag_zero,
            flag_compare,

            getRatio,
        }
    }
}
</script>

